/* App.css */

.app-container {
    background: linear-gradient(to right, #ebf8ff, #e6fffa);
    height: 100vh;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .form-container {
    width: 100%;
    max-width: 600px;
    text-align: center;
    border-radius: 0.5rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: white;
    padding: 1.5rem;
    transition: transform 0.5s;
  }
  
  .form-container:hover {
    transform: scale(1.05);
  }
  
  .title {
    font-size: 2.5rem;
    font-weight: bold;
    color: #4299e1;
    margin-bottom: 1rem;
    animation: bounce 1s infinite;
  }
  
  .textarea {
    border: 1px solid #d1d5db;
    border-radius: 0.25rem;
    width: 100%;
    margin-bottom: 1rem;
    padding: 0.75rem;
    transition: border-color 0.3s, box-shadow 0.3s;
    min-height: 100px;
  }
  
  .textarea:focus {
    border-color: #63b3ed;
    box-shadow: 0 0 0 3px rgba(99, 179, 237, 0.5);
  }
  
  .submit-button {
    background-color: #4299e1;
    color: white;
    padding: 0.75rem;
    border-radius: 0.25rem;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .submit-button:hover {
    background-color: #3182ce;
  }
  
  .disabled-button {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  .answer-container {
    width: 100%;
    max-width: 600px;
    text-align: center;
    border-radius: 0.5rem;
    background-color: white;
    margin-top: 1rem;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    transition: transform 0.5s;
  }
  
  .answer-container:hover {
    transform: scale(1.05);
  }
  
  .answer {
    padding: 1rem;
  }
  
  @keyframes bounce {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  